h1,
ul {
  color: #fff;
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
}

h2,
h4 {
  color: #fff;
  font-family: "Lora Italic", "Times New Roman", Times, serif;
  font-style: italic;
}

p {
  color: #fff;
  font-family: "Roboto light", Arial, Helvetica, sans-serif;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

@keyframes logoGlow {
  from {
    border-radius: 50%;
    box-shadow: 0 0 19px 10px transparent, inset 0 0 19px 10px transparent;
  }
  50% {
    border-radius: 50%;
    box-shadow: 0 0 18px 10px #009ea8, inset 0 0 18px 10px #009ea8;
  }
  to {
    border-radius: 50%;
    box-shadow: 0 0 19px 10px transparent, inset 0 0 19px 10px transparent;
  }
}

@keyframes logoPulse {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}

.button {
  background: transparent;
  border: 3px solid #fff;
  color: #fff;
  cursor: pointer;
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
  font-size: 1rem;
  padding: 10px;
  text-transform: uppercase;
  transition: .5s ease;
}

.button:hover {
  border: 3px solid #009ea8;
  overflow: visible;
  transform: scale(1.05);
}

.navbar {
  background: linear-gradient(to top, rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5), rgba(255, 255, 255, 0));
  bottom: 0;
  height: 100px;
  left: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.navbar-portfolio-grid {
  position: fixed;
  top: 0;
}

.navbar ul {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 10px 0;
  width: 100%;
}

.navbar li {
  display: inline-block;
  padding: 5px;
  position: relative;
  text-shadow: 1px 1px 1px #111;
}

.navbar li:first-child::after, .navbar li:last-child::after {
  background: transparent;
  content: '';
  display: block;
  height: 2px;
  margin: auto;
  margin-top: 3px;
  transition: width .5s ease, background-color .5s ease;
  width: 0;
}

.navbar li:nth-child(2) {
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-name: logoPulse;
}

.navbar li:nth-child(2)::before {
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-name: logoGlow;
  content: '';
  display: block;
  height: 100%;
  opacity: .5;
  pointer-events: none;
  position: absolute;
  transform: translate(-7%, -10%);
  width: 100%;
}

.navbar li:hover::after {
  background: #009ea8;
  width: 100%;
}

.navbar a {
  color: #fff;
  text-decoration: none;
}

.navbar--fixed-top {
  position: fixed;
  top: 0;
}

form {
  color: #009ea8;
}

.inputbox {
  height: 30px;
  width: 250px;
  position: absolute;
  left: 80%;
  transform: translate(-50%, -50%);
}

.inputbox input {
  width: 100%;
  min-width: 0;
  box-sizing: border-box;
  font-size: 16px;
  padding: 6.5px 0;
  border: 0;
  background: none;
  width: 30px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 100%;
  transition: width 0.4s ease-in-out, border-radius 0.4s ease-in-out, padding 0.2s;
  transition-delay: 0s, 0.4s, 0s;
  box-shadow: 0 0 0 1px currentColor;
  color: inherit;
  cursor: pointer;
  padding: 6.5px 0;
  padding-right: 30px;
  font-weight: lighter;
  font-family: 'Roboto';
}

.inputbox input:focus, .inputbox input:valid {
  width: 100%;
  transition-delay: 0.4s, 0s, 0.4s;
  outline: 0;
  border-radius: 0;
  cursor: text;
  padding-left: 6.5px;
}

.inputbox input:focus + .del, .inputbox input:valid + .del {
  pointer-events: all;
  left: calc(100% - 15px);
  transition-delay: 0.4s;
}

.inputbox input:focus + .del:focus, .inputbox input:valid + .del:focus {
  box-shadow: 0 0 0 1px currentColor;
  height: 22.5px;
  width: 22.5px;
}

.inputbox input:focus + .del:before, .inputbox input:valid + .del:before {
  transform: translate(-50%, -50%) rotate(-45deg) scaleY(0.7);
  transition-delay: 0s;
}

.inputbox input:focus + .del:after, .inputbox input:valid + .del:after {
  transform: translate(-50%, -50%) rotate(45deg) scaleY(0.7);
  transition-delay: 0.2s;
}

.inputbox .del {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 30px;
  width: 30px;
  padding: 0;
  background: none;
  border-radius: 100%;
  border: 0;
  pointer-events: none;
  transition: left 0.4s ease-in-out;
  transition-delay: 0s;
  color: inherit;
  cursor: pointer;
}

.inputbox .del:focus {
  outline: none;
}

.inputbox .del:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background: currentColor;
  width: 1px;
  height: 22.5px;
  transform: translate(19.75px, 19.75px) translate(-50%, -50%) rotate(-45deg) scaleY(1);
  transition: transform 0.2s ease-in-out;
  transition-delay: 0.6s;
}

.inputbox .del:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background: currentColor;
  width: 1px;
  height: 22.5px;
  transform: translate(-50%, -50%) rotate(45deg) scaleY(0);
  transition: transform 0.2s ease-in-out;
  transition-delay: 0.4s;
}

.container {
  margin: 0 auto;
  width: 90%;
  max-width: 1280px;
}

.index {
  overflow: hidden;
}

.landing-background {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../images/landing-background.png") no-repeat center center;
  background-size: cover;
  height: 100vh;
}

.landing-content {
  left: 50%;
  position: relative;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.landing-content h1 {
  font-size: 8vw;
  margin-bottom: 5px;
  text-shadow: 1px 1px 2px #111;
}

.landing-content h2 {
  font-size: 3.5vw;
  text-shadow: 1px 1px 2px #111;
}

.portfolio-background {
  height: 100vh;
}

.portfolio-content {
  height: calc(100vh - 100px);
  left: 0;
  position: absolute;
  top: 100vh;
  width: 100%;
}

.portfolio-content a {
  color: #fff;
}

.portfolio-content--fixed-top {
  position: fixed;
  top: 100px;
}

.portfolio-content--left {
  float: left;
  height: 100%;
  margin-left: 5%;
  width: 45%;
}

.portfolio-content--left ul {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-around;
  text-transform: uppercase;
}

.portfolio-content--left li {
  cursor: pointer;
}

.portfolio-content--left li h3 {
  transition: .750s;
}

.project-title__active {
  font-size: 1.5em;
}

.project--1__subtitle,
.project--2__subtitle,
.project--3__subtitle,
.project--4__subtitle,
.project--5__subtitle {
  margin-left: -200px;
  opacity: 0;
  transition: .750s;
}

.project--1__description,
.project--2__description,
.project--3__description,
.project--4__description,
.project--5__description {
  height: 0;
  opacity: 0;
  overflow: hidden;
  transition: opacity .75s ease-out;
}

.portfolio-content--right {
  float: right;
  height: 100%;
  margin-right: 5%;
  width: 45%;
}

.project-description {
  left: 50%;
  position: relative;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.project-description p {
  padding-bottom: 20px;
}

.portfolio-grid {
  column-count: 5;
  column-gap: 5;
}

.portfolio-grid img {
  height: auto;
  width: 100%;
}

.portfolio-grid-item__container {
  margin-bottom: 1em;
  text-align: center;
}

.portfolio-grid-item {
  line-height: 0;
  overflow: hidden;
  position: relative;
  transition: .5s;
}

.portfolio-grid-item:hover {
  transform: scale(1.05);
}

.portfolio-grid-item__overlay {
  align-items: center;
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: .5s;
}

.portfolio-grid-item__overlay:hover {
  opacity: 1;
}

@media (max-width: 1200px) {
  .portfolio-grid {
    column-count: 4;
  }
}

@media (max-width: 1000px) {
  .portfolio-grid {
    column-count: 3;
  }
}

@media (max-width: 800px) {
  .portfolio-grid {
    column-count: 2;
  }
}

@media (max-width: 400px) {
  .portfolio-grid {
    column-count: 1;
  }
}

.portfolio-functions {
  margin: 140px 0 20px;
}

.portfolio-functions__search {
  padding-bottom: 30px;
}
